import React , { Component } from 'react';
import {connect} from 'react-redux';
import { Link }  from 'react-router-dom';
import Payments from './Payments';


class Header extends Component{

  renderContent(){
    switch(this.props.auth){

      case null:
      return 'Still deciding';
      case false:
      return <li><a href="auth/google">Login With Google</a></li>;
      default:
    return [<li key='1'><Payments /></li>,<li style={{margin:'0px 10px'}} key='0'>Credits:{this.props.auth.credits}</li>,<li key='2'><a href="/api/logout">Logout</a></li>];
    }

  }

    render(){
      console.log(this.props)
        return (<nav>
            <div className="nav-wrapper">
              <Link to={this.props.auth?"/surveys":"/dashboard"} className="brand-logo">Emaily</Link>
              <ul id="nav-mobile" className="right">
                {this.renderContent()}
              </ul>
            </div>
          </nav>)
    }
    // <Link to={this.props.auth?"/surveys":"/dashboard"} className="brand-logo">Emaily</Link>

}

function mapStateToProps({auth}){
  return {auth};
}

export default connect (mapStateToProps)(Header);


